.kh_link {
    font-weight: bold;
    text-decoration: none;
    color: var(--col-black);
    transition: color var(--transition);
    font-size: var(--fs-300);
    &:hover {
        color: var(--col-orange-400);
    }
}

.latest {
    &__grid {
        display: grid;
        row-gap: 1rem;
        column-gap: 3rem;
        .latest__card {
            img {
                aspect-ratio: 4 / 1;
                object-fit: cover;
                mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 400 100'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m400,0v55.44c-60.68,28.58-128.47,44.56-200,44.56S60.68,84.02,0,55.44V0h400Z'/%3E%3C/g%3E%3C/svg%3E");
                mask-position: bottom;
                mask-size: cover;
            }
        }
        @media (min-width:768px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, auto);
            .latest__card:nth-of-type(1) {
                grid-row: span 2;
                img {
                    aspect-ratio: 4 / 3;
                    mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 600 400'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m600,0v248.73c-67.95,91.77-177.03,151.27-300,151.27S67.95,340.5,0,248.72V0h600Z'/%3E%3C/g%3E%3C/svg%3E");
                    mask-position: bottom;
                    mask-size: cover;
                }
            }
            .latest__card:not(:nth-of-type(1)) {
                .latest__image {
                    img {
                        aspect-ratio: 4 / 1;
                        object-fit: cover;
                        mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 400 100'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m400,0v55.44c-60.68,28.58-128.47,44.56-200,44.56S60.68,84.02,0,55.44V0h400Z'/%3E%3C/g%3E%3C/svg%3E");
                        mask-position: bottom;
                        mask-size: cover;
                    }
                }
            }
        }
    }
}

.latest__card--short {
    height: auto !important;
    .latest__image {
        img {
            aspect-ratio: 4 / 1;
            object-fit: cover;
            mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 400 100'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m400,0v55.44c-60.68,28.58-128.47,44.56-200,44.56S60.68,84.02,0,55.44V0h400Z'/%3E%3C/g%3E%3C/svg%3E");
        }
    }
}

.guide_card {
    display: block;
    background-color: var(--col-white);
    text-decoration: none;
    color: var(--col-black);
    border-radius: 1rem;
    box-shadow: var(--shadow-out);
    transition: transform var(--transition), box-shadow var(--transition);
    overflow: hidden;
    height: 100%;
    &:hover {
        color: var(--col-black);
        transform: translateY(2px);
        box-shadow: var(--shadow-in);
        .guide_card__img {
            transform: scale3d(0.85, 0.85, 1) rotateX(0) rotateZ(0) translate3d(0,-1rem,0);
        }
    }
    h3 {
        padding: 1rem;
        margin-bottom: 0;
        font-size: var(--fs-400);
    }
    &__content {
        font-size: var(--fs-200);
        margin-bottom: 0;
        padding: 0.5rem 1rem;
    }
    &__image {
        background: var(--gradient-orange);
        perspective: 450px;
        perspective-origin: 50% 50%;
        aspect-ratio: 4 / 3;
        width: 100%;
        display: grid;
        place-content: center;
        mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 600 400'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m600,0v248.73c-67.95,91.77-177.03,151.27-300,151.27S67.95,340.5,0,248.72V0h600Z'/%3E%3C/g%3E%3C/svg%3E");
        mask-size: cover;
        mask-position: center bottom;
    }
    .flash {
        position: absolute;
        top: 1rem;
        left: 1rem;
        padding: 0.5rem;
        border-radius: 100vw;
        text-transform: uppercase;
        font-size: var(--fs-100);
        font-weight: var(--fw-900);
        line-height: 1;
        color: var(--col-black);
    }
    .flash--guide {
        background-color: var(--col-yellow-400);
    }
    &__img {
        transform: scale3d(0.75, 0.75, 1) rotateX(58deg) rotateY(0) rotateZ(340deg) translate3d(0, -1rem, 0) skew(0deg, 0deg);
        transition: transform var(--transition);
    }

}

.infographics {
    &__card {
        display: block;
        background-color: var(--col-white);
        text-decoration: none;
        color: var(--col-black);
        border-radius: 1rem;
        box-shadow: var(--shadow-out);
        transition: transform var(--transition), box-shadow var(--transition);
        overflow: hidden;
        height: 100%;
        text-decoration: none;
        &:hover {
            color: var(--col-black);
            transform: translateY(2px);
            box-shadow: var(--shadow-in);
        }
    }
    &__image {
        aspect-ratio: 1 / 1.1;
        width: 100%;
        mask: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg id='Layer_2' data-name='Layer 2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 600 400'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23fff; stroke-width: 0px; %7D %3C/style%3E%3C/defs%3E%3Cg id='Layer_1-2' data-name='Layer 1'%3E%3Cpath class='cls-1' d='m600,0v248.73c-67.95,91.77-177.03,151.27-300,151.27S67.95,340.5,0,248.72V0h600Z'/%3E%3C/g%3E%3C/svg%3E");
        mask-size: cover;
        mask-position: center;
        overflow: hidden;
        img {
            width: 100%;
            object-fit: cover;
        }
    }
    h3 {
        padding: 1rem;
        margin-bottom: 0;
        font-size: var(--fs-400);
    }
    .swiper-wrapper {
        margin-bottom: 2rem;
    }
}

